.container_loader {
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    direction: rtl;
    background: #333;
}

.loader {
    width: 100px;
    height: 100px;
    display: inline-block;
    position: relative;
}

.loader::after,
.loader::before {
    content: '';
    box-sizing: border-box;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #ffd700;
    position: absolute;
    left: 0;
    top: 0;
    animation: animloader 2s linear infinite;
}

.loader::after {
    animation-delay: 1s;
}

@keyframes animloader {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}

.dropbtn {
    background: #04aa6d;
    border: none;
    font-size: 17px;
    padding: 17px;
    color: #fff;
}

.drobdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    background: #f1f1f1;
    min-width: 160px;
    position: absolute;
    z-index: 1;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.dropdown-content a {
    display: block;
    color: #000;
    text-decoration: none;
    padding: 17px;
    font-size: 18px;
}

.dropdown-content a:hover {
    background: #ddd;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .dropbtn {
    background: #3e8341;
}

@media only screen and (width: 390px) {
    .nimadur {
        display: block;
    }
}